}

p {
  font-size: 2rem;
  color: #000C12;
}

span {
  font-weight: bold;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  z-index: 100;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.barra {
  position: sticky;
  top: 0;
  padding-top: 1rem;
  background-color: #eee;
}
.barra p, .barra a {
  height: auto;
  color: #005A80;
}
