  transition: color 0.3s linear;
}
.barra a:hover {
  color: #01b3ff;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-bottom: 3rem;
  transition: visibility 0.4s ease-in-out, opacity 0.4s ease-in, height 0.4s linear;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-between;
    gap: 3rem;
    margin-bottom: 0;
  }
}

.hidden-nav {
  visibility: hidden;
  height: 0;
  opacity: 0;
}
@media (min-width: 768px) {
  .hidden-nav {
    visibility: visible;
    height: auto;
    opacity: 1;
  }
}

.hero {
  padding: 1rem;
}
@media (min-width: 768px) {
  .hero {
    padding: 10rem;
  }
}
.hero h1, .hero p {
  color: #000C12;
}
.hero p {
  margin: 0;
}
.hero span {
  font-weight: 800;
}

.sobre-mi {
  background-color: #005A80;
  padding: 7rem 0 20rem;
}
.sobre-mi h2, .sobre-mi h4 {
  color: #eee;
  margin: 1rem 0;
}
.sobre-mi p {
  max-width: 80%;
  margin: 2rem auto;
  color: #e1e1e1;
}
.sobre-mi i {
  margin-top: 1rem;
  font-size: 4rem;
  color: #eee;
  transition: all 0.3s;
}
.sobre-mi i:hover {
  transform: scale(1.12);
  color: #d5d5d5;
}

.proyectos {
  background-color: #eee;
  margin-top: -15rem;
  padding: 5rem 0;
  border-bottom: 1px #e1e1e1 solid;
}
.proyectos p {
  width: 60%;
  margin: 0 auto;
}

.paginas {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin: 4rem auto;
}
@media (min-width: 768px) {
  .paginas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .paginas {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.pagina {
  width: 100%;
  height: 23rem;
  margin: 0 auto;
  max-width: 33rem;
  max-height: 25rem;
  display: grid;
  grid-template-rows: minmax(100%, 1fr);
  grid-template-areas: "stack";
  overflow: hidden;
  position: relative;
}
.pagina img, .pagina figcaption {
  grid-area: stack;
}
.pagina img {
  width: 100%;
  height: 100%;
  position: relative;
  inset: 0;
  object-fit: cover;
  transition: all 600ms;
}
.pagina figcaption {
  grid-area: stack;
  background-color: rgba(36, 36, 36, 0.8784313725);
  transform: translateY(calc(100% - 4rem));
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
  transition: all 0.6s;
}
.pagina figcaption h3 {
  margin: 0 auto;
  color: #e1e1e1;
  font-weight: 400;
  transition: all 0.6s;
}
.pagina figcaption p {
  font-size: 1.4rem;
  color: #e1e1e1;
  width: 80%;
  margin: 0 auto;
}
.pagina:hover img {
  transform: scale(1.19);
}
.pagina:hover figcaption {
  transform: none;
  justify-content: center;
}

.slider {
  display: flex;
  align-items: center;
  gap: 4rem;
  width: 100%;
  height: 30rem;
  padding: 0 4rem;
  scroll-snap-type: x mandatory;
  overflow-x: scroll;
  overflow-y: hidden;
}
.slider::-webkit-scrollbar {
  background-color: #005A80;
  width: 7px;
}
.slider img {
  flex: 0 0 100%;
  width: 100%;
  object-fit: cover;
  margin: auto;
  scroll-snap-align: center;
  transition: all 0.3s;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 17rem;
  margin: auto;
}
.card .card__img {
  margin: auto;
  display: flex;
}
.card .card__img:hover img {
  transform: scale(1.09);
}
.card .card__texto {
  margin: 0;
}
.card .card__texto p {
  margin: 0 auto;
}

footer {
  padding: 5rem;
}
footer .media-social .media-social__links {
  display: flex;
