.campo {
  width: 95%;
  max-width: 50rem;
}
.campo label {
  text-align: left;
  display: block;
  color: #1e1e1e;
  margin: 1rem auto;
  font-size: 1.6rem;
}
.campo input,
.campo textarea {
  outline: none;
  padding: 1rem;
  margin: 0 auto;
  border-bottom: 1px solid #e1e1e1;
  border-top: none;
  border-right: none;
  border-left: none;
  background-color: unset;
  color: #1e1e1e;
  width: 100%;
  transition: all 0.3s ease-in-out;
}
.campo input:focus,
.campo textarea:focus {
  border-bottom: 1px #007eb3 solid;
}
.campo input:hover,
.campo textarea:hover {
  border-bottom: 1px #007eb3 solid;
}
.campo textarea {
  height: 10rem;
}

.campo_email-error input,
.campo_nombre-error input {
  border-bottom: 2px solid #BF0502;
}
.campo_email-error input:focus,
.campo_nombre-error input:focus {
  border-bottom: 2px #f10603 solid;
}
.campo_email-error input:hover,
.campo_nombre-error input:hover {
  border-bottom: 2px #f10603 solid;
}

.campo_mensaje-error textarea {
  border-bottom: 2px solid #BF0502;
}
.campo_mensaje-error textarea:focus {
  border-bottom: 2px #f10603 solid;
}
.campo_mensaje-error textarea:hover {
  border-bottom: 2px #f10603 solid;
}

.campo_error label {
  color: #BF0502;
}

.campo_email-correcto input,
.campo_nombre-correcto input {
  border-bottom: 2px solid #329f00;
}
.campo_email-correcto input:focus,
.campo_nombre-correcto input:focus {
  border-bottom: 2px #42d200 solid;
}
.campo_email-correcto input:hover,
.campo_nombre-correcto input:hover {
  border-bottom: 2px #42d200 solid;
}

.campo_mensaje-correcto textarea {
  border-bottom: 2px solid #329f00;
}
.campo_mensaje-correcto textarea:focus {
  border-bottom: 2px #42d200 solid;
}
.campo_mensaje-correcto textarea:hover {
  border-bottom: 2px #42d200 solid;
}

.campo_correcto label {
  color: #329f00;
}

.campo__mensajes .mensaje {
  margin: 1rem 0;
  text-align: center;
}

.boton {
  background-color: transparent;
  padding: 1.5rem 4rem;
  color: #005A80;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: 1px #007eb3 solid;
  border-radius: 25px;
  transition-property: background-color color;
  transition-duration: 0.3s;
  text-align: center;
  margin: 3rem auto;
  width: auto;
}
.boton:hover {
  background-color: #007eb3;
  color: #eee;
  cursor: pointer;
}

.enviar {
  background-color: transparent;
  padding: 1.5rem 4rem;
  color: #005A80;
  font-size: 2rem;
  font-weight: 400;
  display: block;
  border: 1px #007eb3 solid;
  border-radius: 25px;
  transition-property: background-color color;
  transition-duration: 0.3s;
  text-align: center;
  margin: 3rem auto;
  width: auto;
}
.enviar:hover {
  background-color: #007eb3;
  color: #eee;
  cursor: pointer;
}

.btn-website {
  display: block;
  padding: 0.5rem 2rem;
  margin: 0 auto;
  background-color: transparent;
  color: #eee;
  text-align: center;
  border: 1px #eee solid;
  border-radius: 25px;
  transition-property: border color;
  transition-duration: 0.3s;
}
.btn-website:hover {
  border: 1px #aeaeae solid;
  color: #aeaeae;
  cursor: pointer;
}

.btn-bienvenida {
  background-color: transparent;
  padding: 1.5rem 4rem;
  color: #eee;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: 1px #007eb3 solid;
  border-radius: 25px;
  transition-property: background-color color;
  transition-duration: 0.3s;
  text-align: center;
  margin: 3rem auto;
  width: auto;
  position: relative;
}
.btn-bienvenida:hover {
  background-color: #007eb3;
  color: #eee;
  cursor: pointer;
}
.btn-bienvenida:hover::before {
  background-color: #eee;
  top: 50px;
}

.btn-bienvenida::before {
  content: "";
  position: absolute;
  left: 0;
  top: 63px;
  background: #6f6f6f;
  height: 50%;
  width: 100%;
  transform: perspective(4mm) rotateX(40deg) scale(1, 0.5);
  filter: blur(8px);
  transition: 1s ease;
}

.hidden {
  visibility: hidden;
  height: 0;
  display: none;
  opacity: 0;
  transition: all 1s linear;
}

.visible {
  visibility: visible;
  height: auto;
  opacity: 1;
}

