  align-items: center;
  padding: 1rem 0;
  gap: 2rem;
  width: 100%;
  background-color: #128C7E;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.chat .chat__header h4 {
  font-size: 2.2rem;
  margin: 0;
  color: #eee;
}
.chat .chat__header i {
  color: #eee;
  font-size: 3.6rem;
}
.chat .chat__body {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  gap: 2rem;
  margin: 0;
  margin-left: 1rem;
  padding: 4rem 2rem 0;
}
@media (min-width: 480px) {
  .chat .chat__body {
    flex-direction: row;
  }
}
.chat .chat__body img {
  width: 4rem;
  height: 4rem;
  margin: 0;
}
.chat .chat__body p {
  margin: 0;
  text-align: left;
  font-size: 1.6rem;
}
.chat .chat__submit {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 2rem auto 0.5rem;
  padding: 0 2rem 1rem;
  width: 100%;
}
.chat .chat__submit input {
  background-color: transparent;
  padding: 1rem 1.6rem;
  border-radius: 25px;
  border: 1px solid #a1a1a1;
  width: 100%;
  transition: all 0.3s;
}
.chat .chat__submit input:focus, .chat .chat__submit input:hover {
  outline: none;
  border-color: #005A80;
}
.chat .chat__submit button {
  border-radius: 50%;
  border: none;
  width: 4.5rem;
  height: 4.5rem;
  background: linear-gradient(45deg, #f0f0f0, #cacaca);
  box-shadow: 5px -5px 6px rgba(177, 177, 177, 0.4392156863), -5px 5px 6px #ffffff;
  cursor: pointer;
  transition: all 0.3s;
}
.chat .chat__submit button:hover {
  background: #e0e0e0;
  box-shadow: 5px -5px 10px rgba(125, 125, 125, 0.6), -5px 5px 10px #ffffff;
}

.alertas {
  margin: 2rem auto 0.5rem;
}

.error, .exito {
  text-align: center;
  font-weight: 700;
  margin-top: 1rem;
}

.error {
  color: #BF0502;
}

.exito {
  color: #329f00;
}

.notfound {
  height: 100%;
  width: 100%;
  margin: 0px;
  background: linear-gradient(90deg, rgb(47, 54, 64) 23%, rgb(24, 27, 32) 100%);
}

.moon {
  background: linear-gradient(90deg, rgb(208, 208, 208) 48%, rgb(145, 145, 145) 100%);
  position: absolute;
  top: -100px;
  left: -300px;
  width: 900px;
  height: 900px;
  content: "";
  border-radius: 100%;
  box-shadow: 0px 0px 30px -4px rgba(0, 0, 0, 0.5);
}

.moon__crater {
  position: absolute;
  content: "";
  border-radius: 100%;
  background: linear-gradient(90deg, rgb(122, 122, 122) 38%, rgb(195, 195, 195) 100%);
  opacity: 0.6;
}

.moon__crater1 {
  top: 250px;
  left: 500px;
  width: 60px;
  height: 180px;
}

.moon__crater2 {
  top: 650px;
  left: 340px;
  width: 40px;
  height: 80px;
  transform: rotate(55deg);
}

.moon__crater3 {
  top: -20px;
  left: 40px;
  width: 65px;
  height: 120px;
  transform: rotate(250deg);
}

.star {
  background: grey;
  position: absolute;
  width: 5px;
  height: 5px;
  content: "";
  border-radius: 100%;
  transform: rotate(250deg);
  opacity: 0.4;
  animation-name: shimmer;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes shimmer {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.7;
  }
}
.star1 {
  top: 40%;
  left: 50%;
  animation-delay: 1s;
}

.star2 {
  top: 60%;
  left: 90%;
  animation-delay: 3s;
}

.star3 {
  top: 10%;
  left: 70%;
  animation-delay: 2s;
}

.star4 {
  top: 90%;
  left: 40%;
}

.star5 {
  top: 20%;
  left: 30%;
  animation-delay: 0.5s;
}

.error {
  position: absolute;
  left: 100px;
  top: 400px;
  transform: translateY(-60%);
  font-family: "Open Sans", sans-serif;
  color: #005A80;
}

.error__title {
  font-size: 10em;
}

.error__subtitle {
  font-size: 2em;
}

.error__description {
  opacity: 0.8;
  font-weight: 400;
}

.error__button {
  min-width: 7em;
  margin-top: 3em;
  margin-right: 0.5em;
  padding: 1rem 2rem;
  outline: none;
  border: 1px #007eb3 solid;
  background-color: transparent;
  border-radius: 25px;
  color: #005A80;
  cursor: pointer;
  transition-property: background-color color;
  transition-duration: 0.3s;
  font-size: 1.6rem;
  font-weight: 400;
  font-family: "Open Sans", sans-serif;
}
.error__button:hover {
  background-color: #007eb3;
  color: #eee;
}

